<template>
  <el-dialog
    title="错误信息"
    :visible.sync="dialogVisible"
    @close="handleDialogClose">
    <p>{{ internalErrorMessage }}</p>
  </el-dialog>
</template>

<script>
export default {
  props: {
    errorMessage: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      dialogVisible: false,
      internalErrorMessage: '' // 添加一个新的data属性
    }
  },
  methods: {
    showError (message) {
      this.internalErrorMessage = message // 更新内部消息而不是prop
      this.dialogVisible = true
    },
    handleDialogClose () {
      this.dialogVisible = false
    }
  }
}
</script>



