<template>
  <div class="navs">
    <router-link to="/">首页</router-link>
    <router-link to="/bizhi">壁纸</router-link>
    <router-link to="/manhua">漫画</router-link>
    <router-link to="/user/collectList"  v-if="isLoggedIn2">收藏</router-link>
  </div>
</template>

<script>
export default {
  name: 'Navbar',
  data () {
    return {
      isLoggedIn2: false
    }
  },
  created () {
    this.isLoggedIn2 = !!this.$getToken()
  }
}
</script>
<!-- 公用导航栏 -->
<!-- 你可以在这里或在全局样式文件中保留你的样式 -->
<style>
.navs {
  display: flex;
  justify-content: space-around;
}
.navs a{
  text-decoration: none;
    font-weight: 700;
    color: #999;
    -webkit-transition: color .3s;
    transition: color .3s;
    font-size: x-large;
}
/* 其他样式 */
</style>



