<template>
    <div class="main">
      <button class="btn"  v-if="isLoggedIn2 && showButton" :disabled="isButtonDisabled"  @click="soucang()">收藏</button>
      <div id="imgsdiv">
        <img v-for="(imgUrl, index) in imageUrls" :src="imgUrl" :key="index" referrer="no-referrer|origin|unsafe-url" onerror="this.style.display='none'">
      </div>
      <div v-if="isButtonVisible" id="bbutton" class="bbutton" @click="scrollToTop">
        ↑
      </div>
      <div class="loadmore">
        <button class="btn" @click="getmore(10)">加载更多</button>
        <button class="btn" @click="getmore(100)">加载100</button>
        <button class="btn" @click="jump(10)">跳跃10</button>
        <button class="btn" @click="jump(100)">跳跃100</button>
      </div>
      <error-dialog ref="errorDialog"></error-dialog>
    </div>
</template>

<script>
import jQuery from 'jquery'
import axios from 'axios'
import ErrorDialog from '@/components/ErrorDialog.vue'
export default {
  name: 'Detail',
  components: {
    ErrorDialog
  },
  data () {
    return {
      isButtonDisabled: false,
      showButton: true,
      isLoggedIn2: false,
      lastCall: 0,
      isButtonVisible: false,
      urlParam: null,
      imageUrls: [] // 存储图片URL的数组
    }
  },
  created () {
    this.isLoggedIn2 = !!localStorage.getItem('token')
    this.urlParam = this.$route.params.url // 从$route.params获取url参数
    console.log(this.urlParam)
    this.initializeImages()
  },
  mounted () {
    window.addEventListener('scroll', this.checkTop)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.checkTop)
  },
  methods: {
    initializeImages () {
      this.imageUrls.push(this.urlParam)
    },
    soucang () {
      this.isButtonDisabled = true
      let token = this.$getToken()
      const params = new URLSearchParams()
      params.append('token', token)
      params.append('url', this.urlParam)
      axios.post(`${this.$apiBaseUrl}collect`,
        params
      )
        .then(response => {
          console.log(response)
          // 处理响应
          if (response.data.code === 0) {
            this.showButton = false
          } else {
            this.isButtonDisabled = false
          }
        })
        .catch(error => {
          // 处理错误
          console.error('注册请求错误', error)
        })
        .finally(() => {
          this.isButtonDisabled = false
        })
    },
    getmore (x) {
      if (x <= 10) {
        x = 10
      }
      // 找到最后一张图
      var lastimg = jQuery('img').last().attr('src')
      // 取图 名
      var imgname = lastimg.substring(lastimg.lastIndexOf('/') + 1)

      // 提取名字中的数字 123
      var reg1 = /\d+/g
      var imgnum = imgname.match(reg1)
      var num = imgnum.pop()
      for (var i = 1; i <= x; i++) {
        var newnum = num - 0 + i
        var name = String(newnum)
        if (num.length > String(newnum).length) {
          // 长度不一样 前面需要加0
          while (num.length > name.length) {
            name = '0' + name
          }
        }
        var reg = new RegExp('(.*)' + num)
        var n1 = imgname.replace(reg, '$1' + name)
        var n = n1.replace(' ', '%20')

        var newn = lastimg.replace(imgname, n)
        this.imageUrls.push(newn)
      }
    },
    jump (x) {
      // 找到最后一张图
      var lastimg = jQuery('img').last().attr('src')
      // 取图 名
      var imgname = lastimg.substring(lastimg.lastIndexOf('/') + 1)
      // 提取名字中的数字 123
      var reg1 = /\d+/g
      var imgnum = imgname.match(reg1)
      var num = imgnum.pop()
      // 最后一个数字

      var newnum = num - 0 + x
      var name = String(newnum)
      if (num.length > String(newnum).length) {
        // 长度不一样 前面需要加0
        while (num.length > name.length) {
          name = '0' + name
        }
      }
      var reg = new RegExp('(.*)' + num)
      var n1 = imgname.replace(reg, '$1' + name)
      var n = n1.replace(' ', '%20')
      var newn = lastimg.replace(imgname, n)
      this.imageUrls.push(newn)
    },
    checkTop () {
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      const windowHeight = document.documentElement.clientHeight
      const scrollHeight = document.documentElement.scrollHeight
      this.isButtonVisible = scrollTop > 0
      // 距离底部距离加大
      const now = Date.now()
      if (scrollTop + windowHeight + 5 >= scrollHeight) {
        if (now - this.lastCall > 1000) { // 1秒的节流时间间隔
          this.getmore(10)
          this.lastCall = now
        }
      }
    },
    scrollToTop () {
      window.scrollTo({ top: 0, behavior: 'smooth' })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main {
  display: flex;
  flex-direction: column;
}
img {
  width: 100%;
}
img {
  display: block;
}
#imgsdiv .width100 {
  width: 100%;
}
.bbutton {
  position: fixed;
  bottom: 200px;
  right: 20px;
  border: 1px solid rgb(120, 120, 120);
  border-radius: 6px;
  padding: 5px;
  background-color: rgb(170, 170, 170);
}
.loadmore {
  width: 100%;
  display: flex;
  justify-content: space-around;
  position: fixed; /* 固定位置，使其悬浮 */
  bottom: 20px; /* 距离底部20px */
  left: 50%; /* 水平居中 */
  transform: translateX(-50%); /* 确保准确居中 */
}
</style>



