<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div class="postlist">
      <router-link :to="{ name: 'detail' ,params: { id: 1 } }">detail1</router-link>
      <router-link :to="{ name: 'detail' ,params: { id: 2 } }">detail2</router-link>
      <router-link :to="{ name: 'detail' ,params: { id: 3 } }">detail3</router-link>
      <router-link :to="{ name: 'detail' ,params: { id: 4 } }">detail4</router-link>

    </div>
  </div>
</template>

<script>
export default {
  name: 'List',
  data () {
    return {
      msg: 'List页面'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  color: #42b983;
}
</style>



