<template>
  <div class="mhmain">
    <router-link
      v-for="picUrl in mhpicUrls"
      :key="picUrl.url"
      :to="{ name: 'mh', params: { url: picUrl.url }}"
    >
      <img class="mhitem" :src="picUrl.url" />
    </router-link>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'mhList',
  data () {
    return {
      mhpicUrls: {}
    }
  },
  mounted () {
    this.getpicUrls()
  },
  methods: {
    getpicUrls () {
      let token = this.$getToken()
      const params = new URLSearchParams()
      params.append('token', token)
      axios.post(`${this.$apiBaseUrl}systuijian`,
        params
      )
        .then(response => {
        // 处理响应
          if (response.data.code === 0) {
            let res = response.data.data
            console.log('成功', res)
            this.mhpicUrls = res
          // 登录成功的处理逻辑
          } else {
          // 登录失败的处理逻辑
            this.$refs.errorDialog.showError(response.data.msg)
          }
        })
        .catch(error => {
          // 处理错误
          console.error('请求错误', error)
        })
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  display: block;
  max-height: 300px;
  overflow: hidden;
  margin-bottom: 10px;
}
.mhmain {
  background-color: gray;
}
.mhitem {
  width: 100%;
  height: auto;
  margin-top: 20px;
}
</style>



