// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import ErrorDialog from '@/components/ErrorDialog.vue'

Vue.component('error-dialog', ErrorDialog)
Vue.use(ElementUI)
Vue.config.productionTip = false
// 设置全局API地址
Vue.prototype.$apiBaseUrl = 'http://jie.ueuo.com/api.php?act='
Vue.prototype.$getToken = function () {
  const basetoken = localStorage.getItem('token')
  if (!basetoken) return null
  const { data, expiry } = JSON.parse(basetoken)
  const now = new Date().getTime()
  if (now >= expiry) {
    localStorage.removeItem('token') // 清除过期数据
    return null // 数据已过期
  }
  return data
}
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



