import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import About from '@/components/About'
import My from '@/components/My'
import Mh from '@/components/Mh'
import MhList from '@/components/mh/List'
import MhDetail from '@/components/mh/Detail'
import Post from '@/components/Post'
import Postlist from '@/components/post/List'
import PostDetail from '@/components/post/Detail'
import Err from '@/components/Err'
import Bizhi from '@/components/Bizhi'
import User from '@/components/User'
import Login from '@/components/user/Login.vue'
import CollectList from '@/components/user/CollectList.vue'
import Register from '@/components/user/Register.vue'

// import jQuery from 'jquery'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: HelloWorld
    },
    {
      path: '/bizhi',
      name: 'bizhi',
      component: Bizhi
    },
    {
      path: '/user',
      component: User,
      // 二级路由
      children: [
        {
          path: 'login',
          component: Login
        },
        {
          path: 'collectList',
          name: 'CollectList',
          component: CollectList
        },
        {
          path: 'register',
          component: Register
        }
      ]
    },
    {
      path: '/post',
      component: Post,
      // 二级路由
      children: [
        {
          path: '/',
          component: Postlist
        },
        {
          path: 'detail',
          name: 'detail',
          component: PostDetail
        }
      ]
    },
    {
      path: '/my',
      component: My
    },
    {
      path: '/manhua',
      component: Mh,
      children: [
        {
          path: '/',
          name: 'mhList',
          component: MhList
        },
        {
          path: '/mh',
          name: 'mh',
          component: MhDetail
        }
      ]
    },
    {
      path: '/about',
      component: About
    },
    {
      path: '*',
      component: Err
    }
  ]
})



