<template>
  <div class="login-container">
    <h1>注册</h1>
    <form @submit.prevent="submitForm">
      <div class="input_row">
        <label for="username">用户名:</label>
        <input type="text" id="username" required v-model="credentials.username" minlength="4">
      </div>
      <div class="input_row">
        <label for="password">密码:</label>
        <input type="password" id="password" v-model="credentials.password" required minlength="6">
      </div>
      <div class="input_row">
        <label for="confirmPassword">确认密码:</label>
        <input type="password" id="confirmPassword" v-model="credentials.confirmPassword" required minlength="6">
      </div>
      <div class="input_row">
        <p>验证码: {{ captchaQuestion }}</p>
        <input type="text" v-model="userInput" placeholder="输入答案">
      </div>
      <!-- 其他注册所需的字段 -->
      <button class="btn" type="submit" :disabled="isButtonDisabled">注册</button>
    </form>
    <error-dialog ref="errorDialog"></error-dialog>
  </div>
</template>
<script>
import axios from 'axios'
import ErrorDialog from '@/components/ErrorDialog.vue'

export default {
  components: {
    ErrorDialog
  },
  data () {
    return {
      isButtonDisabled: false,
      credentials: {
        username: '',
        password: '',
        confirmPassword: '' // 添加确认密码字段
      },
      userInput: '',
      num1: Math.floor(Math.random() * 10),
      num2: Math.floor(Math.random() * 10)
      // ... 其他数据 ...
    }
  },
  computed: {
    captchaQuestion () {
      return `${this.num1} + ${this.num2} = ?`
    },
    captchaAnswer () {
      return this.num1 + this.num2
    }
  },
  methods: {
    resetCaptcha () {
      this.num1 = Math.floor(Math.random() * 10)
      this.num2 = Math.floor(Math.random() * 10)
      this.userInput = ''
    },
    submitForm () {
      // 验证两次输入的密码是否一致
      if (this.credentials.password !== this.credentials.confirmPassword) {
        this.$refs.errorDialog.showError('两次输入的密码不一致')
        // 重新生成验证码
        this.resetCaptcha()
        return
      }
      if (parseInt(this.userInput) === this.captchaAnswer) {
        // 在这里继续表单提交的逻辑
        this.register()
      } else {
        this.$refs.errorDialog.showError('验证码错误，请重试！')
        // 重新生成验证码
        this.resetCaptcha()
      }
    },
    register () {
      this.isButtonDisabled = true
      // console.log(this.$apiBaseUrl)
      // 定义登录逻辑
      const params = new URLSearchParams()
      params.append('user_name', this.credentials.username)
      params.append('pwd', this.credentials.password)

      axios.post(`${this.$apiBaseUrl}register`,
        params
      )
        .then(response => {
        // 处理响应
          if (response.data.code === 0) {
            console.log('成功', response.data)
            let token = response.data.data.token
            const now = new Date().getTime() // 获取当前时间的时间戳
            const cacheDuration = 3600000 * 24 // 设置缓存时间，例如1小时 (3600000毫秒)
            const dataToStore = {
              data: token, // 您想要缓存的数据
              expiry: now + cacheDuration // 当前时间 + 缓存持续时间
            }
            localStorage.setItem('token', JSON.stringify(dataToStore))
            window.location.href = '/'
            // this.$router.push('/')
            // 登录成功的处理逻辑
          } else {
            this.isButtonDisabled = false
            // 登录失败的处理逻辑
            this.$refs.errorDialog.showError(response.data.msg)
            this.resetCaptcha()
          }
        })
        .catch(error => {
          // 处理错误
          console.error('注册请求错误', error)
        })
    }
  }
}
</script>
<style scoped>
.input_row {
  display: flex;
  align-items: center;
  margin: 10px;
}

.input_row label {
  width: 100px; /* 或您期望的宽度 */
  text-align: right;
  margin-right: 10px;
}

.input_row input {
  flex-grow: 1; /* 输入框占据剩余空间 */
}

.login-container {
  /* ... 其他样式 ... */
}
</style>



