<template>
  <div class="mhmain">
    <input type="number" id="kl" v-model="kl" required min="1" max="50">
    <div v-show="showContent">
      <router-link
        v-for="picUrl in mhpicUrls"
        :key="picUrl.url"
        :to="{ name: 'mh', params: { url: picUrl.url }}"
      >
      <hr>
      <p class="pic_id">{{picUrl.id}}</p>
        <img class="mhitem" :src="picUrl.url" />
      </router-link>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'CollectList',
  data () {
    return {
      kl: 0,
      mhpicUrls: {},
      showContent: false
    }
  },
  mounted () {
    this.getpicUrls()
  },
  watch: {
    kl (newVal) {
      const currentHour = new Date().getHours()
      this.showContent = parseInt(newVal) === currentHour
    }
  },
  methods: {
    getpicUrls () {
      let token = this.$getToken()
      const params = new URLSearchParams()
      params.append('token', token)
      axios.post(`${this.$apiBaseUrl}collectList`,
        params
      )
        .then(response => {
        // 处理响应
          if (response.data.code === 0) {
            let res = response.data.data
            console.log('成功', res)
            this.mhpicUrls = res
          // 登录成功的处理逻辑
          } else {
          // 登录失败的处理逻辑
            this.$refs.errorDialog.showError(response.data.msg)
          }
        })
        .catch(error => {
          // 处理错误
          console.error('请求错误', error)
        })
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  color: yellow;
  text-decoration-line: none;
}
.pic_id {
  text-align: center;
}
a {
  display: block;
  max-height: 300px;
  overflow: hidden;
  margin-bottom: 10px;
}
.mhmain {
  background-color: gray;
}
.mhitem {
  width: 100%;
  height: auto;
  margin-top: 20px;
}
</style>



