<template>
  <div class="main">
    <div class="user_bar" v-if="!isLoggedIn">
      <router-link class="btn" to="/user/login">登录</router-link>
      <router-link class="btn" to="/user/register">注册</router-link>
    </div>
    <div class="user_bar" v-else>
      <button class="btn" @click="logout()">退出</button>
    </div>
    <div class="tuijian">
      <div class="tj_item">
        公告内容
        <p>
            壁纸资源量不定期更新
        </p>
        <p>
            漫画资源每日刷新
        </p>
        <!-- <img src="/static/imgs/vx.jpg" alt="Description"> -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '默认首页',
      isLoggedIn: false
    }
  },
  created () {
    this.isLoggedIn = !!this.$getToken()
  },
  methods: {
    logout () {
      localStorage.removeItem('token')
      window.location.href = '/'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main{
  margin-top: 10px;
}
.tuijian{
  display: flex;
  flex-direction: column;
  padding-top: 10px;
}
.tj_item{
  width: 100%;
  height: 100px;
  background: rgb(203, 195, 195);
  margin-bottom: 10px;
}
.item_div{
  width: 100%;
}
.user_bar{
  display: flex;
  justify-content: space-evenly;
}
img {
  width: 150px;
  height: 150px;
  display: inline-block;
}
</style>



