<template>
  <div id="app">
    <Navbar/>
    <keep-alive>
      <router-view v-if="isRouteCached"></router-view>
    </keep-alive>
    <router-view v-if="!isRouteCached"></router-view>
  </div>
</template>

<script>
import Navbar from './components/Navbar.vue'

export default {
  name: 'App',
  components: {
    Navbar // 注册Navbar组件
  },
  computed: {
    isRouteCached () {
      // 定义需要缓存的路由名称列表
      const cacheRoutes = ['mhList', 'CollectList']
      // 检查当前路由是否在列表中
      console.log(cacheRoutes.includes(this.$route.name), this.$route.name)
      return cacheRoutes.includes(this.$route.name)
    }
  }
}
</script>

<style>
/* 您的样式 */
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
#app{
  font-family: Avenir,Helvetica,Arial,sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
}
.btn {
  padding: 10px 20px; /* 按钮内边距 */
  background-color: #8d9093; /* 按钮背景颜色 */
  color: white; /* 按钮文字颜色 */
  border: none; /* 去除边框 */
  border-radius: 5px; /* 圆角边框 */
  cursor: pointer; /* 鼠标悬浮时显示手形指针 */
  text-align: center; /* 文字居中 */
  text-decoration: none; /* 去除文本下划线 */
  display: inline-block; /* 行内块元素 */
  transition: background-color 0.3s, color 0.3s; /* 过渡效果 */
}

.btn:hover {
  background-color: #9eabb8; /* 鼠标悬浮时的背景颜色 */
}

.btn:active {
  background-color: #eef0f3; /* 鼠标点击时的背景颜色 */
}
input{
  border-radius: 5px;
  height: 30px;
}
</style>



