<template>
  <div>
    <div class="main">
      <img v-for="picUrl in picUrls" :src="picUrl" :key="picUrl" />
    </div>
    <div v-if="isButtonVisible" id="bbutton" class="bbutton" @click="scrollToTop">
      ↑
    </div>
    <!-- 后续 按钮悬浮于页面底部 -->
    <div class="loadmore" v-if="userPate > 0">
      <button class="btn" @click="paperAdd(1)">加一页</button>
      <!-- <button class="btn" @click="paperAdd(10)">跳10页</button> -->
      <!-- <button class="btn" @click="paperAdd(50)">跳50页</button> -->
    </div>
    <error-dialog ref="errorDialog"></error-dialog>
  </div>
</template>

<script>
import jQuery from 'jquery'
import axios from 'axios'
import ErrorDialog from '@/components/ErrorDialog.vue'
export default {
  components: {
    ErrorDialog
  },
  data () {
    return {
      lastCall: 0,
      isButtonVisible: false,
      paper: 1,
      userPate: 0,
      picUrls: []
    }
  },
  mounted () {
    this.getmore(this.paper)
    if (this.paper === 1) {
      this.getUserConf()
    }
    // 这里存在问题,其他页面拖动滚动条,这边也会触发
    window.addEventListener('scroll', this.checkTop)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.checkTop)
  },
  methods: {
    getUserConf () {
      let token = this.$getToken()
      const params = new URLSearchParams()
      params.append('token', token)
      axios.post(`${this.$apiBaseUrl}getUserConf`,
        params
      )
        .then(response => {
        // 处理响应
          if (response.data.code === 0) {
            console.log('成功', response.data)
            this.userPate = parseInt(response.data.data.page)
          // 登录成功的处理逻辑
          } else {
          // 登录失败的处理逻辑
            this.$refs.errorDialog.showError(response.data.msg)
          }
        })
        .catch(error => {
          // 处理错误
          console.error('请求错误', error)
        })
    },
    paperAdd (add) {
      this.paper += add
      // 后续检查账号,
      if (this.userPate <= this.paper) {
        this.$refs.errorDialog.showError('没有更多了!')
        return
      }
      console.log(this.userPate, this.paper)
      this.getmore(this.paper)
    },
    getmore (page = 1) {
      return jQuery.ajax({
        url: 'https://ai.gmnsxg.shop/webh/web/imgs/pageIsWeb',
        method: 'POST',
        data: {
          title: '',
          pageNum: page,
          pageSize: 10,
          navigatePages: 0
        }
      }).then(response => {
        if (response.code !== 200) {
          return false
        }
        let list = response.data.list

        let pics = list.filter(item => item.title !== '重要通知').map(item => item.multiplefile)
        if (page === 1) {
          pics.splice(0, 2)
        }
        let pArr = []
        pics.forEach(value => {
          pArr.push(...value.split(',&~'))
        })
        this.picUrls = [...this.picUrls, ...pArr] // 更新图片URL数组
      }).catch(error => {
        console.error('Error:', error)
        return false
      })
    },
    checkTop () {
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      const windowHeight = document.documentElement.clientHeight
      const scrollHeight = document.documentElement.scrollHeight
      this.isButtonVisible = scrollTop > 0
      // 距离底部距离加大
      const now = Date.now()
      if (scrollTop + windowHeight + 5 >= scrollHeight) {
        if (now - this.lastCall > 1000) { // 1秒的节流时间间隔
          this.paperAdd(1)
          this.lastCall = now
        }
      }
    },
    scrollToTop () {
      window.scrollTo({ top: 0, behavior: 'smooth' })
    }
  }
}
</script>

<style scoped>
/* CSS 样式 */
.main {
  display: flex;
  flex-direction: column;
}
img {
  width: 100%;
}
.bbutton {
  position: fixed;
  bottom: 200px;
  right: 20px;
  border: 1px solid rgb(120, 120, 120);
  border-radius: 6px;
  padding: 5px;
  background-color: rgb(170, 170, 170);
}
.loadmore {
  width: 100%;
  display: flex;
  justify-content: space-around;
  position: fixed; /* 固定位置，使其悬浮 */
  bottom: 20px; /* 距离底部20px */
  left: 50%; /* 水平居中 */
  transform: translateX(-50%); /* 确保准确居中 */
}
</style>



